.work-experience-card{
    background: #130f2a;
    border-radius: 0.65rem;
    border: 1.5px solid #6751b9;
    padding: 1.5rem;
    margin: 0 1rem;
}

.work-experience-card::before{
    content: ' ';
    position: absolute;
    width: 1.2rem;
    height: 1.2rem;
    border-radius: 1.2rem;
    right: 100%;
    background-color: #6751b9;
    border-left: 2px solid #6751b9;
    margin-top: -1.7rem;
    margin-right: -2.7rem;
}

.work-experience-card h6{
    font-size: 1.05rem;
    font-weight: 500;
    margin-bottom: 0.7rem;
    margin-left: 1.5rem;
    margin-bottom: -0.7rem;
}

.work-duration{
    display: inline-block;
    font-size: 0.7rem;
    font-weight: 400;
    background-color: rgba(103, 81, 185, 0.5);
    border-radius: 0.3rem;
    padding: 0.4rem 0.6rem;
    margin-bottom: 1.3rem;
}

.work-experience-card ul li{
    list-style: none;
    font-size: 0.8rem;
    font-weight: 400;
    margin-bottom: 0.5rem;
    position: relative;
}

.work-experience-card ul li::after{
    content: " ";
    width: 0.5rem;
    height: 0.5rem;
    border-radius: 0.5rem;
    background-color: #6751b9;
    position: absolute;
    left: -1.3rem;
    top: 0.35rem;
}

@media (max-width: 768px){
    .work-experience-card{
        margin: 0;
    }

    .work-experience-card h6{
        font-size: 0.95rem;
    }

    .work-experience-card ul li{
        font-size: 0.75rem;
    }
}
